.infoBox {
  flex: 1;
  cursor: pointer;
  min-width: 130px;
  text-align: center;
}
.infoBox:not(:last-child) {
  margin-right: 10px;
}

.infoBox__cases {
  color: #cc1034;
  font-weight: 600;
  font-size: 1.75rem;
  margin-bottom: 0.5rem;
}

.infoBox__total {
  color: #6c757d;
  font-weight: 700 !important;
  font-size: 0.8rem !important;
  margin-top: 15px !important;
}
.infoBox--selected {
  border-top: 10px solid rgb(28, 153, 55);
}
.infoBox--red {
  border-color: rgb(247, 58, 58);
}
.infoBox__cases--green {
  color: rgb(28, 153, 55);
}
