.table {
  margin: 20px 0;
  overflow: scroll;
  height: 400px;
  color: #6a5d5d;
  background-color: #fff;
}
.table tr {
  display: flex;
  justify-content: space-between;
}
.table td {
  padding: 0.5rem;
  border: none;
}
.table tr:nth-of-type(odd) {
  background-color: #f3f2f8;
}
.table::-webkit-scrollbar {
  display: none;
}
