.map {
  height: 500px;
  background-color: #ffffff;
  padding: 1rem;
  border-radius: 20px;
  margin: 16px 0;
  box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
}

.map .leaflet-container {
  height: 100%;
  border-radius: 12px;
}

.info-flag img {
  width: 100px;
  /* object-fit: contain; */
  border-radius: 5px;
}
.info-name {
  font-size: 18px;
  font-weight: bold;
  color: #555555;
}

.info-flag {
  height: 100px;
  width: 100%;
  background-size: cover;
  border-radius: 8px;
  object-fit: contain;
}
.info-confirmed,
.info-recovered,
.info-deaths {
  font-size: 16px;
  margin-top: 5px;
}

.info-container {
  width: 150px;
}
